import React, {useState} from 'react';
import './App.scss';
import ToDoItem from './Components/ToDoitems/ToDoitem.js';
import InputDescription from './Components/InputDescription/InputDescription.js'
import Confirmed from './Components/confirmed/confirmed.js';

const useStateWithLocalStorage = localStorageKey => {
    localStorage.clear();
    const arr = [{'id': 0, 'description': 'Взяти цього хлопаку на роботу', 'completed': false}];
    const [todos, setTodos] = useState(localStorage.getItem(localStorageKey) || arr);
    console.log(todos);
    React.useEffect(() => {
        localStorage.setItem(localStorageKey, todos[0])
        // eslint-disable-next-line
    }, [todos]);
    console.log(localStorage.getItem(localStorageKey));
    return [todos, setTodos];
};

const App = () => {
    const [deletindex, setDeleteindex] = useState('');
    const [visible, setVisible] = useState(false);
    const [value, setValue] = useState('');    
    const [todos, setTodos] = useStateWithLocalStorage('myTodoLocalStorage');
    const onChange = event => {
        setValue(event.target.value);
    }
    const onClick = () =>{
        if (value !== '') {        
            let newtodos = todos.map(i =>{
                return i;
            });
            newtodos.push({id:todos.length, description: value, completed:false});
            setTodos(newtodos);   
        }
        setValue('');
    }
    const ListChange = id =>{
        let newtodos = todos.map(i =>{
            return i;
        });
        const index = newtodos.map(item => item.id).indexOf(id);
        if(newtodos[index].completed === false){
            newtodos[index].completed = true;
        }
        else{
            newtodos[index].completed = false;
        }         
        setTodos(newtodos);
    }

    const tryDelete = (id) =>{
        setDeleteindex(id);
        if (visible === false) {
            setVisible(true);
        }
    }

    const ClickNo = () =>{
        setVisible(false);
    }

    const deleteDescription = id =>{
        setVisible(false);
        let newtodos = todos.map(i =>{
            return i;
        });
        const index = newtodos.map(item => item.id).indexOf(id);        
        newtodos.splice(index, 1 );
        setTodos(newtodos);
    }

    const activeTasks = todos.filter(task => task.completed === false)
    const completedTasks = todos.filter(task => task.completed === true)

    const finalTasks = [...activeTasks,...completedTasks].map(item => {
        return(
            <ToDoItem
                key = {item.id}
                description = {item.description}
                completed = {item.completed}
                checkboxstyle = {item.completed ? 'checkmark active': "checkmark disable"}
                descriptionStyle = {item.completed ? 'description disable': "description active"}
                ListChange = {() => {ListChange(item.id)}}
                deleteDescription = {() => {tryDelete(item.id)}}
            />
        )
    })

    return(
        <div className="App">
            <Confirmed 
                styles = {visible ? 'confirmed-active' : 'confirmed-disable'}
                ClickNo = {ClickNo}
                ClickOk = {() => {deleteDescription(deletindex)}}   
            />
            <h1 className="Title">ToDo List</h1>
            <InputDescription
                value = {value}
                onChange = {onChange}
                onClick = {onClick}
            />
            <div className="wrap-list">
                {finalTasks}
            </div>
        </div>
    );
};

export default App;
